<template>
    <div class="block">
        <!--工具条-->
        <!--栅格布局 -->
        <div class="toolbar">
            <el-col :span="4">
                <!--<span class="">地区:</span>-->
                <el-cascader placeholder='省/市/区' change-on-select :options="options" v-model="selectedOptions" @change="dizhiChange">
                </el-cascader>
                <!--<p>省I1D:{{obj[0]}}市ID:{{obj[1]}}区ID:{{obj[2]}}</p>-->
            </el-col>
            <el-col :span="4">
                <!--<span class="">类型:</span>-->
                <el-cascader placeholder='小学' change-on-select :options="options" v-model="selectedOptions" @change="dizhiChange">
                </el-cascader>
            </el-col>
            <el-col :span="4">
                <!--<span class="">状态:</span>-->
                <el-cascader placeholder='启用' change-on-select :options="options" v-model="selectedOptions" @change="dizhiChange">
                </el-cascader>
            </el-col>
            <el-col :span="4">
                <!--<span class="">关键字:</span>-->
                <el-cascader placeholder='输入编号和名称' change-on-select :options="options" v-model="selectedOptions" @change="dizhiChange">
                </el-cascader>
            </el-col>
            <el-col :span="8">
                <el-button type="primary" size="large" @click.native='addFormVisible = true'>新增学校</el-button>
                <el-button type="primary">导入</el-button>
                <el-button type="primary" size="small">导出</el-button>
                <el-button type="primary" size="mini">超小按钮</el-button>
            </el-col>
        </div>
    
        <!--显示列表-->
        <el-table highlight-current-row v-loading="loading_1" element-loading-text="拼命加载中" :data="showList" style="width: 100%;text-align:left">
            <el-table-column prop="name" label="学校名称" width="150">
            </el-table-column>
            <el-table-column label="学校编号" prop="id" width="150">
            </el-table-column>
            <el-table-column label="类型" :formatter="tableType" prop="type" width="100">
            </el-table-column>
            <el-table-column label="创建时间" prop="time" width="180" sortable>
            </el-table-column>
            <el-table-column label="状态" prop="status" :formatter="tableStatus" width="100" sortable>
            </el-table-column>
            <el-table-column label="地址" prop="address" min-width='250'>
            </el-table-column>
            <el-table-column label="班级列表" width="120">
                <template scope="scope">
                    <el-button @click='onCheckClassList(scope.row)' size="small">查看</el-button>
                </template>
            </el-table-column>
            <el-table-column label="教师列表" width="120">
                <template scope="scope">
                    <el-button size="small">查看</el-button>
                </template>
            </el-table-column>
            <el-table-column label="操作" width="220">
                <template scope="scope">
                    <el-button type="primary" size="small" @click="handleEdit(scope.$index, scope.row)">修改</el-button>
                    <el-button size="small">屏蔽</el-button>
                    <el-button size="small" type="danger" @click="handleDel(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--编辑页面-->
        <el-dialog title="学校信息修改" v-model="dialogVisible" :close-on-click-modal="false">
            <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
                <el-form-item label="学校名称" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="学校编号" prop="id">
                    <el-input v-model="editForm.id" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="学校地址">
                    <el-input type="textarea" v-model="editForm.address"></el-input>
                </el-form-item>
                <el-form-item label="创建时间" prop="time">
