                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button :plain="true" type="info" @click.native="editSubmit">确 定</el-button>
            </span>
        </el-dialog>
        <!--新增页面-->
        <el-dialog title="添加学校" v-model="addFormVisible" :close-on-click-modal="false">
            <el-form :model="addSchool" label-width="80px">
