                    <el-cascader style="margin-bottom:10px;" placeholder='省/市/区' change-on-select :options="options" v-model="selectedOptions" @change="dizhiChange">
                    </el-cascader>
                    <el-input placeholder="详细地址" v-model="addSchool.address"></el-input>
                </el-form-item>
                <el-form-item label="学校编号" prop="id">
                    <el-input v-model="addSchool.id" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="学校名称" prop="name">
                    <el-input v-model="addSchool.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="学校类型" prop="type">
                    <el-input v-model="addSchool.type" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="状态">
                    <el-radio-group v-model="addSchool.status">
                        <el-radio class="radio" :label="1">启用</el-radio>
                        <el-radio class="radio" :label="0">禁用</el-radio>
                    </el-radio-group>
                </el-form-item>
    
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addFormVisible = false">取消</el-button>
                <el-button type="primary" @click.native="addFormVisible = false">提交</el-button>
            </div>
        </el-dialog>
    
        <div class="block">
            <span class="demonstration">完整功能</span>
            <el-pagination @size-change="pagingSizeChange" @current-change="pagingCurrentChange" :page-sizes="[5, 10, 15, 20]" :page-size="10" layout="total, sizes, prev, pager, next, jumper" :total="schoolList.length">
            </el-pagination>
        </div>
        <router-view></router-view>
    </div>
</template>
