        onCheckClassList: function (school) {
            console.log(school.id)
            if (school && school.id) {
                // this.$router.push('/school/classList?id=' + school.id);
                this.$router.push({ path: '/school/classList', query: { 'id': school.id } })
            } else {
                this.$message({
                    showClose: true,
                    message: 'ID跑哪去了？',
                    type: 'error'
                });
            }
        }
        ,
        //新增页面

        //显示编辑界面
        handleEdit: function (index, row) {
            this.dialogVisible = true;
            // console.log(this.editForm)
            this.editForm = Object.assign({}, row);
            console.log(this.editForm)
        },
        tableType: function (row, column) {
            return row.type == 0 ? '小学' : row.type == 1 ? '中学' : '大学';
        },
        tableStatus: function (row, column) {
            return row.status == 1 ? '启用' : row.status == 0 ? '屏蔽' : '未定义';
        },
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    this.$confirm('确认提交吗？', '提示', {}).then(() => {
                        let para = Object.assign({}, this.editForm);
                        para.time = (!para.time || para.time == '') ? '' : util.formatDate.format(new Date(para.time), 'yyyy-MM-dd');

                        console.log(para.time)
                        console.log(para)
                        this.dialogVisible = false;
                    });
                }
            });
        }
        ,
        //删除
        handleDel: function (index, row) {
            this.$confirm('确认删除该记录吗?', '提示', {
                type: 'warning'
            })
        },
        handleClose(done) {
            this.$confirm('确认关闭？')
                .then(_ => {
                    done();
                })
                .catch(_ => { });
        }
        ,
        //分页方法
        pagingSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.page_size = val;
            // console.log(this.page_size)
            this.star = (this.currentPage - 1) * this.page_size;
            this.end = this.star + this.page_size;
            this.showList = this.schoolList.slice(this.star, this.end)
        },
        pagingCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.currentPage = val;
            this.star = (val - 1) * this.page_size;
            this.end = this.star + this.page_size;
            this.showList = this.schoolList.slice(this.star, this.end)
        },
    },
