            },]







        //调用方法
        this.showList = this.schoolList.slice(this.star, this.end)
    }
};
</script>
 <style lang="scss">
.block {
    .toolbar {
        margin: 10px 0;
        padding: 20px 0px;
        height: 40px;
        width: 100%;
        background-color: #f2f2f2;
    }
}


.el-table__header-wrapper {
    table {
        th {
            text-align: center;
        }
        ;
    }
}

.el-table__body-wrapper {
    table {
        tr {
